figure {
  text-align: center;
  margin: 20px 0px;

  img {
    display: block;
    max-width: 90vw;
    margin: 0px auto;
  }

  figcaption {
    color: #333;
    font-size: 1.1em;
    margin: 10px auto;
  }
}

.collage {
  padding: 20px 0px;
  text-align: center;

  img {
    margin: 10px;
    max-width: 95vw;
    vertical-align: middle;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
}

video {
  display: block;
  margin: 0px auto;
  max-width: 95vw;
}
